.homeContainer {
    width: 100%;
    height: 100%;
    @include flexColCenter;
    color: white;
    position: relative;

    p {
        font-size: clamp(18px, 2vw, 30px);
        padding: 16px;
        text-align: center;
    }

    .homeHook {
        margin-bottom: 24px;
    }

    .homeHookEnd {
        margin-top: 24px;
    }

    a {
        position: absolute;
        bottom: clamp(4vh, 14vw, 14vh);
        left: 50%;
        transform: translateX(-50%);
        height: 50px;
        width: 200px;
        border-radius: 40px;
        color: rgb(18, 18, 39);
        background-color: #fff;
        font-size: 22px;
        font-weight: 800;
        @include flexColCenter;
        border: 2px solid transparent;
        transition: .4s;


        &:hover {
            background-color: transparent;
            border: 2px solid #fff;
            color: #fff;

        }
    }
}