.mealCard {
    border-radius: 14px;
    padding: 12px 0;
    min-width: 200px;
    max-width: 280px;
    flex: 1 0 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    background-color: rgba(255, 255, 255, .94);
    transition: .3s;
    cursor: pointer;
    margin-bottom: 3px;

    h3 {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
        font-size: 16px;
        width: 90%;
        text-align: center;
        color: rgb(20, 20, 46);
    }

    img {
        width: 90%;
        border-radius: 14px;
        object-fit: cover;
    }

    &:hover {
        scale: 1.1;
    }
}

.mealInstructions {
    text-align: justify;
    padding: 0 18px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 8;
    overflow: hidden;
    margin-bottom: 8px;
}