.cookingContainer {
    @include flexColCenter;
    justify-content: flex-start;
    height: 100%;
    width: 100%;
    gap: 40px;

    h1 {
        color: #fff;
        font-family: font-title;
        font-size: 72px;
        margin-top: 32px;
        text-shadow: 2px 2px 2px rgb(0, 0, 0);
        letter-spacing: 2px;
    }

    input {
        width: clamp(280px, 20vw, 20vw);
        min-height: 40px;
        border-radius: 20px;
        border: 2px solid rgba(217, 217, 217, 0.841);
        text-align: center;
        font-size: 18px;
        margin-bottom: 24px;
        box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.468);
    }

    .logoContainer {
        h1 {
            position: relative;
            font-family: font-title;
            font-size: clamp(72px, 4vw, 4vw);
            text-align: center;
            text-shadow: 5px 5px 5px rgb(0, 0, 0);

            &::after {
                content: "";
                position: absolute;
                left: .1vw;
                top: .6vw;
                transform: translate(-50%, -50%) rotate(-6deg);
                height: clamp(44px, 2.4vw, 2.4vw);
                width: clamp(44px, 2.4vw, 2.4vw);
                background-image: url(../../assets/img/toque.png);
                background-position: center;
                background-size: cover;

            }
        }
    }
}

.recipesContainer {
    @include flexRowCenter;
    align-items: flex-start;
    gap: 20px;
    flex-wrap: wrap;
    max-width: 2000px;
}