.logoContainer {
    h1 {
        position: relative;
        font-family: font-title;
        font-size: clamp(72px, 12vw, 12vw);
        text-align: center;
        text-shadow: 5px 5px 5px rgb(0, 0, 0);

        &::after {
            content: "";
            position: absolute;
            left: .8vw;
            top: 1.8vw;
            transform: translate(-50%, -50%) rotate(-5deg);
            height: clamp(44px, 6vw, 8vw);
            width: clamp(44px, 6vw, 8vw);
            background-image: url(../../assets/img/toque.png);
            background-position: center;
            background-size: cover;

        }
    }
}