.recipeContainer {
    @include flexColCenter;
    justify-content: flex-start;
    height: 100%;
    width: 100%;
    gap: 40px;
    color: #fff;

    .recipePictureContainer {
        min-width: 300px;
        min-height: 300px;
        border-radius: 14px;
        background-color: rgba(255, 255, 255, 0.888);
        @include flexRowCenter;
        margin-top: 24px;
    }

    .recipePicture {
        width: 276px;
        height: 276px;
        border-radius: 14px;
    }

    h2 {
        font-family: font-title;
        font-size: 72px;
        text-align: center;
        color: rgb(18, 18, 39);
        background-color: rgba(255, 255, 255, 0.888);
        padding: 0 40px;
        border-radius: 60px;
        letter-spacing: 2px;
    }

    main {
        @include flexRowCenter;
        align-items: flex-start;
        width: 80vw;
        height: 100%;
        gap: 40px;

        .recipeDetails {
            max-width: 18vw;
            min-width: 18vw;
            height: 100%;
            display: flex;
            flex-direction: column;
            position: fixed;
            left: 6vw;

            .recipePrep {
                font-size: 24px;
                font-weight: 500;
                @include flexRowCenter;
                background-color: rgba(15, 15, 23, 0.65);
                padding: 20px;
                border-radius: 10px 10px 0 0;
                display: flex;
                flex-direction: column;

                &::after {
                    content: "";
                    background-color: rgba(255, 255, 255, 0.733);
                    height: 1px;
                    width: 40%;
                    margin-top: 1vw;
                }
            }

            .recipeIngredients {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: .6vw;
                background-color: rgba(15, 15, 23, 0.65);
                border-radius: 0 0 10px 10px;
                padding-bottom: 1vw;
            }
        }

        .recipeInstructions {
            max-width: 48vw;
            min-width: 48vw;
            font-size: 20px;
            background-color: rgba(15, 15, 23, 0.432);
            padding: 20px;
            border-radius: 6px;

            .instructionsContent {
                white-space: pre-wrap;
                text-align: center;
                line-height: 50px;
            }

            .instructionsEnd {
                font-family: font-title;
                text-align: center;
                font-size: 48px;
                margin: 32px 0;
                letter-spacing: 2px;
            }
        }

        .recipeExtended {
            max-width: 18vw;
            min-width: 18vw;
            font-size: 24px;
            display: flex;
            flex-direction: column;
            gap: 60px;
            font-weight: 500;
            background-color: rgba(15, 15, 23, 0.65);
            padding: 20px;
            border-radius: 10px;
            padding-bottom: 64px;
            position: fixed;
            right: 6vw;

            .recipeYoutube {
                width: 100%;
                text-align: center;

                p {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 8px;

                }

                svg {
                    font-size: 84px;
                }

                iframe {
                    width: 100%;
                    border-radius: 4px;
                }
            }

            .recipeSource {
                width: 100%;
                text-align: center;
            }

            a {
                height: 50px;
                width: 200px;
                border-radius: 8px;
                font-size: 22px;
                font-weight: 800;
                color: rgb(18, 18, 39);
                margin-bottom: 12px;
                background-color: white;
                border: 2px solid transparent;
                padding: 12px;
                transition: .4s;

                &:hover {
                    background-color: transparent;
                    border: 2px solid #fff;
                    color: #fff;

                }
            }

            .placeholder {
                width: 100%;
                height: 300px;
                display: flex;
                align-items: center;
                justify-content: center;

            }
        }
    }


}