@font-face {
    font-family: "font-1";
    src: url(../assets/fonts/Roboto-Regular.ttf);
}

@font-face {
    font-family: "font-2";
    src: url(../assets/fonts/Montserrat-VariableFont_wght.ttf);
}

@font-face {
    font-family: "font-title";
    src: url(../assets/fonts/title.ttf);
}

$font-1: "font-1",
sans-serif;
$font-2: "font-2",
sans-serif;

$color-1: #61dafb;
$color-2: salmon;
$color-3: #282c34;
$white: rgb(251, 251, 251);

@mixin flexColCenter {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@mixin flexRowCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

// RESET

*,
*::after,
*::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: black;

    &:hover {
        text-decoration: none;
        color: black;
    }
}

li {
    list-style-type: none;
}

// LAYOUT

::selection {
    color: white;
    background: rgb(223,
71,
71);
  }

html,
body,
#root,
.appContent {
    height: 100%;
}

.layoutWrapper {
    width: 100%;
}

// To delete when project start
.pagePlaceholder {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

body {
    background-image: url(../assets/img/vegies.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    font-family: $font-2;
}

.appContent {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}